.header,
.stories {
  margin-left: 16px;
  margin-right: 16px;
}

.header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-top: 28px;
}
.header .logo {
  width: 45px;
  height: 28px;
}
.header .nav .nav-toggle {
  background-color: transparent;
  border: 0;
  cursor: pointer;
  padding: 0;
}
.header .nav .nav-links {
  display: none;
  list-style: none;
}
.header .nav .nav-links.expanded {
  background-color: var(--almostwhite);
  display: block;
  position: fixed;
  bottom: 0;
  top: 0;
  right: 0;
  width: 256px;
  font-size: 18px;
  padding-left: 40px;
}
.header .nav .nav-links.expanded .nav-toggle {
  display: block;
  width: 100%;
  text-align: right;
  padding: 27px 21px 86px 0;
}
.header .nav .nav-links.expanded li a {
  color: var(--darkspaceblue);
}
.header .nav .nav-links.expanded li:not(:last-of-type) {
  margin-bottom: 24px;
}
.header .nav li > a {
  color: var(--gunmetal);
  text-decoration: none;
}

.stories {
  margin-top: 32px;
}
.stories .stories-featured img {
  margin-bottom: 24px;
  width: 100%;
}
.stories .stories-featured .featured-heading {
  color: var(--darkspaceblue);
  font-size: 45px;
  font-weight: 800;
  line-height: 40px;
  margin-bottom: 16px;
}
.stories .stories-featured .featured-desc {
  margin-bottom: 37px;
}
.stories .stories-featured a {
  color: var(--Almost-White, #fffdfa);
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px; /* 171.429% */
  letter-spacing: 4.375px;
  width: 185px;
  height: 48px;
  flex-shrink: 0;
  background-color: var(--lightvermillion);
  text-decoration: none;
  text-transform: uppercase;
  padding: 13px 35px;
}
.stories .stories-featured a:hover {
  background-color: var(--darkspaceblue);
}
.stories .stories-new {
  background-color: var(--darkspaceblue);
  padding: 24px 21px;
}
.stories .stories-new .new-heading {
  color: var(--yellow);
  /* Heading (M) */
  font-family: Inter;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px; /* 100% */
  margin-bottom: 35px;
}
.stories .stories-new .new-articles li:not(:last-of-type) {
  padding-bottom: 29px;
  border-bottom: 1px solid #5e607a;
  margin-bottom: 31px;
}
.stories .stories-new .new-articles .article-title {
  color: var(--almostwhite);
  /* Heading (S) */
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 800;
  line-height: 24px; /* 120% */
  text-decoration: none;
  margin-bottom: 8px;
}
.stories .stories-new .new-articles .article-title:hover {
  color: var(--yellow);
}
.stories .stories-new .new-articles .article-desc {
  color: var(--silver);
}
.stories .stories-top {
  counter-reset: top-story;
}
.stories .stories-top .top-articles > li {
  display: flex;
}
.stories .stories-top .top-articles > li:not(last-of-type) {
  margin-bottom: 32px;
}
.stories .stories-top .top-articles_img {
  width: 100px;
  height: 129px;
  margin-right: 24px;
}
.stories .stories-top .top-articles_link {
  color: var(--darkspaceblue);
  /* Heading (XS) */
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 800;
  line-height: 24px; /* 133.333% */
  text-decoration: none;
  margin-bottom: 9px;
  display: block;
}
.stories .stories-top .top-articles_link:hover {
  color: var(--lightvermillion);
}
.stories .stories-top .top-articles_link:before {
  display: block;
  content: "0" counter(top-story);
  counter-increment: top-story;
  color: var(--silver);
  /* Heading (M) */
  font-family: Inter;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px; /* 100% */
  margin-bottom: 12px;
}
.stories .stories-top .top-articles_desc {
  color: var(--gunmetal);
  /* Body */
  font-family: Inter;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px; /* 173.333% */
}

@media (min-width: 700px) {
  .header {
    margin: 40px auto;
    max-width: 1110px;
    padding: 0 45px 0;
  }
  .header .logo {
    width: 64px;
    height: 40px;
  }
  .header .nav .nav-toggle {
    display: none;
  }
  .header .nav .nav-links {
    display: flex;
    justify-content: space-between;
    width: 431px;
    padding: 0;
  }
  .header .nav .nav-links li > a {
    color: var(--gunmetal);
  }
  .header .nav .nav-links li > a:hover {
    color: var(--lightvermillion);
  }
}
@media (min-width: 1440px) {
  .header {
    margin-top: 89px;
    padding: 0;
  }
}